import React from 'react';
import './About.scss';
import { IconContext } from 'react-icons';
import SkillsIcons from '../SkillsIcons/SkillsIcons'
import { FaGithub, FaEnvelope, FaLinkedin, FaAngleDown, FaFile } from 'react-icons/fa';
